/*
Project     : Skeletonboard
Filename    : _body.scss
Version     : 1.0
Author      : Mark Dijkstra
Last change : --/--/----
*/
body {
  background-color: #f0f1f5;
  color: #555555;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

/*
Project     : Skeletonboard
Filename    : _header.scss
Version     : 1.0
Author      : Mark Dijkstra
Last change : --/--/----
*/
.header-main {
  width: 100%;
  float: left;
}
.header-main__top {
  height: 70px;
  background: #454d66;
  color: #ffffff;
}
.header-main__logo a {
  float: left;
  width: auto;
  height: 100%;
  padding: 0 30px;
  line-height: 70px;
  position: relative;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
}
.header-main__nav {
  float: right;
  width: auto;
  position: relative;
}
.header-main__nav nav {
  float: left;
  width: auto;
  height: 70px;
  padding: 0 15px;
}
.header-main__nav nav a {
  text-decoration: none;
  height: 60px;
  float: left;
  line-height: 68px;
  padding: 0 15px;
  color: #ffffff;
}
.header-main__nav nav a.active__nav {
  text-decoration: underline;
}
.header-main__user {
  width: 70px;
  height: 70px;
  float: right;
  display: none;
}
.header-main__user img {
  width: 70px;
  height: 70px;
  float: right;
}
.header-main__bottom {
  height: 100px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  padding-top: 30px;
  background-color: #ffffff;
  border-bottom-color: #e6e9f0;
}
.header-main__bottom .x3_container--xl {
  height: 40px;
}
.header-main__heading {
  float: left;
  width: auto;
}
.header-main__heading h3 {
  height: 40px;
  line-height: 38px;
}
.header-main__subnav {
  float: right;
}

/*
Project     : Skeletonboard
Filename    : _content.scss
Version     : 1.0
Author      : Mark Dijkstra
Last change : --/--/----
*/
.content-main {
  width: 100%;
  float: left;
  min-height: calc(100vh - 182px);
}
.content-main__inner {
  padding: 30px 15px;
}

.heading {
  margin: 30px 0;
}

/* vendor */
.x3_card {
  margin-bottom: 30px;
  border: 1px solid #dbdee6;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(52, 59, 68, 0.07);
}

/*
Project     : Skeletonboard
Filename    : _footer.scss
Version     : 1.0
Author      : Mark Dijkstra
